import { Component, OnInit } from '@angular/core';
import { NotifyService } from '../sharedFolder/services/notify.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  blogs = [];
  message;
  searchValue = "";

  constructor(private notifyService: NotifyService) { }

  ngOnInit() {
    this.getAllBlogs();
  }

  getAllBlogs() {
    fetch("http://localhost:3000/api/blogs")
      .then((response) => {
        return response.json();
      })
      .then(responseData => {
        if (responseData.success) {
          this.blogs = responseData.data;
          this.message = this.blogs.length == 0 ? "No blogs." : "";
          // this.notifyService.notificationMessage(responseData.message, 'success');
        }
        else {
          this.notifyService.notificationMessage(responseData.message, 'danger');
        }
      })
      .catch(error => {
        this.notifyService.notificationMessage("Error", 'danger');
      })
  }

  onSearch() {
    let searchValue = this.searchValue;
    if (searchValue === undefined || searchValue === null || searchValue.trim() === "") {
      this.getAllBlogs();
    }
    else {
      this.getByUser(searchValue);
    }
  }

  getByUser(searchValue) {
    fetch(`http://localhost:3000/api/blogs/${searchValue}`)
      .then((response) => {
        return response.json();
      })
      .then(responseData => {
        if (responseData.success) {
          this.blogs = responseData.data;
          this.message = this.blogs.length == 0 ? "No blogs." : "";
          // this.notifyService.notificationMessage(responseData.message, 'success');
        }
        else {
          this.notifyService.notificationMessage(responseData.message, 'danger');
        }
      })
      .catch(error => {
        this.notifyService.notificationMessage("Error", 'danger');
      })
  }
}

