import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { SessionStorageService } from 'angular-web-storage';
import { AuthService } from '../sharedFolder/services/auth.service';
import { NotifyService } from '../sharedFolder/services/notify.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  constructor(private router: Router, private sessionStorage: SessionStorageService, private notifyService: NotifyService) { }

  ngOnInit() {
  }

  login(form) {
    let email = form.value.email;
    let password = form.value.password;
    let body = {
      email, password
    }

    fetch("http://localhost:3000/auth/login", {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify(body)
    })
      .then((response) => {
        return response.json();
      })
      .then(responseData => {
        if (responseData.success) {
          this.notifyService.notificationMessage(responseData.message, 'success');
          this.sessionStorage.set('token', responseData.token, 1, 'h');
          this.sessionStorage.set('email', email, 1, 'h');
          this.router.navigate(["blogs"]);
        }
        else {
          this.notifyService.notificationMessage(responseData.message, 'danger');
          this.router.navigate(["register"]);
        }
      })
      .catch((error) => {
        this.notifyService.notificationMessage("Error", 'danger');
        console.log('error :', error);
      })
  }

}
