import { Component, OnInit } from '@angular/core';
import { SessionStorageService } from 'angular-web-storage';
import { Router } from '@angular/router';
import { NotifyService } from '../sharedFolder/services/notify.service';

@Component({
  selector: 'app-blog-create',
  templateUrl: './blog-create.component.html',
  styleUrls: ['./blog-create.component.css']
})
export class BlogCreateComponent implements OnInit {

  constructor(private sessionService: SessionStorageService, private router: Router, private notifyService: NotifyService) { }

  ngOnInit() {
  }

  onCreate(form) {

    let body = {
      "title": form.value.title,
      content: form.value.content
    }
    let token = this.sessionService.get("token");

    fetch("http://localhost:3000/api/blog", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${token}`
      },
      body: JSON.stringify(body)
    })
      .then((response) => {
        return response.json();
      })
      .then(responseData => {
        if (responseData.success) {
          console.log('responseData.message :', responseData.message);
          this.notifyService.notificationMessage(responseData.message, 'success');
          this.router.navigate(["blogs"]);
        }
        else {
          if (responseData.statusCode === 401) {
            this.router.navigate(["session-expired"]);
          }
          this.notifyService.notificationMessage(responseData.message, 'danger');
        }
      })
      .catch(error => {
        this.notifyService.notificationMessage("Error", 'danger');
      })
  }
}
