import { Component, OnInit } from '@angular/core';
import { SessionStorageService } from 'angular-web-storage';
import { ActivatedRoute, Router } from '@angular/router';
import { NotifyService } from '../sharedFolder/services/notify.service';

@Component({
  selector: 'app-blog-update',
  templateUrl: './blog-update.component.html',
  styleUrls: ['./blog-update.component.css']
})
export class BlogUpdateComponent implements OnInit {

  blogId;
  blogDetails = {};

  constructor(private sessionService: SessionStorageService, private route: ActivatedRoute, private notifyService: NotifyService, private router: Router) { }

  ngOnInit() {
    this.route.params.subscribe((params) => {
      this.blogId = params.blogId;
      this.getBlogDetail();
    })
  }

  getBlogDetail() {
    fetch(`http://localhost:3000/api/blog/${this.blogId}`)
      .then((response) => {
        return response.json();
      })
      .then(responseData => {
        if (responseData.success) {
          this.blogDetails = responseData.data;
        } else {
          this.notifyService.notificationMessage(responseData.message, 'danger');
        }
      })
      .catch((error) => {
        this.notifyService.notificationMessage('Error', 'danger');
      })
  }

  onUpdate(form) {

    let body = {
      title: form.value.title,
      content: form.value.content
    }
    let token = this.sessionService.get('token');

    fetch(`http://localhost:3000/api/blog/${this.blogId}`, {
      method: 'PUT',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${token}`
      },
      body: JSON.stringify(body)
    })
      .then((response) => {
        return response.json();
      })
      .then((responseData) => {
        if (responseData.success) {
          this.notifyService.notificationMessage(responseData.message, 'success');
          this.router.navigate(["blogs"]);
        } else {
          this.notifyService.notificationMessage(responseData.message, 'danger');
        }
      })
      .catch((error) => {
        this.notifyService.notificationMessage('Error', 'danger');
      })
  }

}
