import { Component, OnInit } from '@angular/core';
import { SessionStorageService } from 'angular-web-storage';
import { NotifyService } from '../sharedFolder/services/notify.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-blog',
  templateUrl: './blog.component.html',
  styleUrls: ['./blog.component.css']
})
export class BlogComponent implements OnInit {

  email;
  message;
  blogs = [];

  constructor(private sessionService: SessionStorageService, private notifyService: NotifyService, private router: Router) { }

  ngOnInit() {
    this.getBlogsByUserName();
  }

  getBlogsByUserName() {
    this.email = this.sessionService.get("email");
    fetch(`http://localhost:3000/api/blogs/${this.email}`)
      .then((response) => {
        return response.json();
      })
      .then((responseData) => {
        if (responseData.success) {
          this.blogs = responseData.data;
          //this.notifyService.notificationMessage(responseData.message, 'success');
          this.message = this.blogs.length == 0 ? "No blogs found" : "";
        }
        else {
          this.notifyService.notificationMessage(responseData.message, 'danger');
        }
      })
      .catch(error => {
        this.notifyService.notificationMessage("Error", 'danger');
        console.log('getBlogsByUserName() error :', error);
      })
  }

  onDelete(blogId) {
    let token = this.sessionService.get('token');
    fetch(`http://localhost:3000/api/blog/${blogId}`, {
      method: "DELETE",
      headers: {
        "Authorization": `Bearer ${token}`
      }
    })
      .then(response => {
        return response.json();
      })
      .then(responseData => {
        if (responseData.success) {
          this.notifyService.notificationMessage(responseData.message, 'success');
          this.getBlogsByUserName();
        }
        else {
          this.notifyService.notificationMessage(responseData.message, 'danger');
        }
      })
      .catch(error => {
        this.notifyService.notificationMessage("Error", 'danger');
        console.log('onDelete() error :', error);
      })
  }


  onUpdate(blogId) {
    this.router.navigate(['blogs', 'update', blogId]);
  }

}
